staticClass:"carousel-text"},[_c('h5',[_vm._v("Graph Neural Networks for Taxi Fare and Surge Prediction")]),_c('p',[_vm._v(" The Yellow Taxi Trip Records contain data about taxi trips including pickup and dropoff locations, date and time of pickup and dropoff, distance traveled, and the associated fare of the trip. This data has inherent graph structure in which the nodes are the different pickup/dropoff locations and the edges are the directed trips between them. Due to the increasing advances of Graph Neural Networks (GNNs) in recent years and the advent of efficient frameworks like Deep Graph Library (DGL), we evaluated the performance of GNNs against other classical machine learning methods to assess the viability of GNNs as an accurate model which leverages the inherent graph structure in the data used for these tasks. ")])])]),_c('div',{staticClass:"carousel-item taxi-carousel-item"},[_c('img',{staticClass:"rounded img-fluid",attrs:{"src":require("../../public/assets/fare-surge-graph-pred.png"),"alt":"Second slide"}}),_c('div',{staticClass:"carousel-text"},[_c('h5',[_vm._v("Results")]),_c('p',[_vm._v(" Using the raw yellow taxi trip data from January 2019-June 2019, we contrived two datasets to pose node and regression problems for graphical methods. Using GraphSage GNNs, we explored and benchmarked a new application of GNNs to taxi data against classical ML approaches. For fare prediction, the GNN model performed slightly worse than Linear Regression, Random Forests, and a Fully-Connected Neural Network (FC NN), with the FC NN performing the best, however, it is highly-parameterized. For surge prediction, the GNN performed slightly better than a FC NN. For both taxi prediction tasks, we demonstrated that a less complex GNN model can perform comparably to a highly-parameterized FC NN. ")])])])])])])])}]

